import { Router } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
import "rxjs/add/operator/catch";
import { SettingsService } from "../settings/settings.service";
import { ServiceLocator } from "../service-locator";
import { convertToCSV } from "../helper/helper";
import { Compte } from "../../auth/metier/compte.model";
import { CompteService } from "../../auth/compte/compte.service";
import { Observable } from "rxjs/Observable";
import { Subscription } from "rxjs/Subscription";
import { LoadingSpinnerService } from "../../loading_spinner/loading-spinner";
import { MenuItems } from "../menu/menu-items/menu-items";

export abstract class
  BaseComponent {
  protected settings: SettingsService;
  protected router: Router;
  protected toastrService: ToastrService;
  protected spinnerService: LoadingSpinnerService;
  protected compteService: CompteService;
  protected timer: Subscription;
  protected photo: string;
  protected DEFAULT_PHOTO: string = "assets/img/user/client.png";


  constructor() {
    this.settings = ServiceLocator.injector.get(SettingsService);
    this.router = ServiceLocator.injector.get(Router);
    this.toastrService = ServiceLocator.injector.get(ToastrService);
    this.compteService = ServiceLocator.injector.get(CompteService);
    this.spinnerService = ServiceLocator.injector.get(LoadingSpinnerService);
    this.timer;
    this.photo = this.DEFAULT_PHOTO;
  }

  protected showError(status: number, message: string | any) {
    switch (status) {
      case 401:
        this.router.navigate(['/session/loginone']);
        this.toastrService.error('Votre session a t expire !', 'Accs refus');
        break;
      default:
        this.toastrService.error(message);
        console.log(message);
    }
  }

  protected showInfo(info: string | any) {
    this.toastrService.success(info);
  }

  protected showWarning(info: string | any) {
    this.toastrService.warning(info);
  }

  protected download(dataList: Array<any>) {
    var csvData = convertToCSV(dataList);
    var a = document.createElement("a");
    a.setAttribute('style', 'display:none;');
    document.body.appendChild(a);
    var blob = new Blob([csvData], { type: 'text/csv;charset=UTF-8' });
    var url = window.URL.createObjectURL(blob);
    a.href = url;
    a.download = 'export.csv';
    a.click();
  }

  downloadFile(data: Blob) {
    var blob = new Blob([data], { type: 'application/pdf' });
    var url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  protected hasAuthority(autorisations: any): boolean {
    return this.compteService.hasAuthority(autorisations);
  }

  protected logout(): void {
    localStorage.removeItem('jwt');
    this.timer.unsubscribe();
    this.router.navigate(['/session/loginone']);
  }

  protected isLoggedIn(): boolean {
    let isLogged: boolean = false;
    let token: string = localStorage.getItem('jwt');

    if (token !== null) {
      isLogged = true;
    }

    return isLogged;
  }

  private uploadPhoto(evt) {
    const files = evt.target.files;
    const file = files[0];

    if (files && file) {
      const reader = new FileReader();

      reader.onload = this._handleReaderLoaded.bind(this);

      reader.readAsBinaryString(file);
    }
  }

  _handleReaderLoaded(readerEvt) {
    const binaryString = readerEvt.target.result;
    this.photo = "data:image/png;base64," + btoa(binaryString);
    // console.log(this.photo);
  }

  protected selectFile(event) {
    this.uploadPhoto(event);
  }

  protected deletePhoto() {
    if (!confirm("Etes-vous de vouloir supprimer la photo !!!")) {
      return;
    }
    this.photo = this.DEFAULT_PHOTO;
  }

  protected isAbleToDeletePhoto() {
    let controle = false;

    if (this.photo === this.DEFAULT_PHOTO) {
      controle = false;
    } else {
      controle = true;
    }

    return controle;
  }

  protected getUserPhoto() {
    return this.compteService.getUserPhoto();
  }

}



