import { Component, OnDestroy, OnInit, ViewChild, ViewEncapsulation, } from '@angular/core';
import { MenuItems } from '../core/menu/menu-items/menu-items';
import { BreadcrumbService } from 'ng2-breadcrumb/ng2-breadcrumb';
import { PageTitleService } from '../core/page-title/page-title.service';
import { TranslateService } from 'ng2-translate/ng2-translate';
import { NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { MediaChange, ObservableMedia } from "@angular/flex-layout";
import * as Ps from 'perfect-scrollbar';
import { TourService } from 'ngx-tour-ng-bootstrap';
import { BaseComponent } from "../core/component/base-component";
// Timer : Scheduled :
import 'rxjs/add/observable/interval';
import { Observable } from "rxjs/Observable";

declare var $: any;

const screenfull = require('screenfull');

@Component({
  selector: 'chankya-layout',
  templateUrl: './main-material.html',
  styleUrls: ['./main-material.scss'],
  encapsulation: ViewEncapsulation.None
})
export class MainComponent extends BaseComponent implements OnInit, OnDestroy {

  private _router: Subscription;
  header: string;
  currentLang = 'en';
  url: string;
  themeSkinColor: any = "light";
  dark: boolean;
  boxed: boolean;
  collapseSidebar: boolean;
  compactSidebar: boolean;
  customizerIn: boolean = false;
  chatWindowOpen: boolean = false;
  chatSidebar: boolean = false;
  sidebarClosed: boolean = false;

  private _mediaSubscription: Subscription;
  isMobile: boolean = false;
  private _routerEventsSubscription: Subscription;
  public innerWidth: any;


  @ViewChild('sidenav') sidenav;

  _opened: boolean = true;
  _mode: string = "push";
  _closeOnClickOutside: boolean = false;
  _showBackdrop: boolean = false;

  public _toggleOpened(): void {
    this._opened = !this._opened;
  }

  constructor(public tourService: TourService, public menuItems: MenuItems, private breadcrumbService: BreadcrumbService, private pageTitleService: PageTitleService, public translate: TranslateService, private media: ObservableMedia) {
    super();
    const browserLang: string = translate.getBrowserLang();
    translate.use(browserLang.match(/en|fr/) ? browserLang : 'en');

    // Timer : Scheduled :
    this.loadTimerScheduled();
  }

  ngOnInit() {
    this.innerWidth = window.innerWidth;
    console.log(this.innerWidth);
    this.pageTitleService.title.subscribe((val: string) => {
      this.header = val;
    });

    this._router = this.router.events.filter(event => event instanceof NavigationEnd).subscribe((event: NavigationEnd) => {
      this.url = event.url;
    });

    if (this.url != '/session/login' && this.url != '/session/register' && this.url != '/session/forgot-password' && this.url != '/session/lockscreen') {
      const elemSidebar = <HTMLElement>document.querySelector('.sidebar-container ');

      /** Perfect scrollbar for sidebar menu **/
      if (window.matchMedia(`(min-width: 960px)`).matches) {
        Ps.initialize(elemSidebar, { wheelSpeed: 2, suppressScrollX: true });

      }

      /** Perfect scrollbar for chat window **/
      const elemChatbar = <HTMLElement>document.querySelector('.chat-inner ');
      if (window.matchMedia(`(min-width: 960px)`).matches) {
        Ps.initialize(elemChatbar, { wheelSpeed: 2, suppressScrollX: true });

      }
    }

    if (this.media.isActive('xs') || this.media.isActive('sm')) {
      this._mode = 'over';
      this._closeOnClickOutside = true;
      this._showBackdrop = true;
      this._opened = false;
      this.sidebarClosed = false;

    }

    this._mediaSubscription = this.media.asObservable().subscribe((change: MediaChange) => {
      let isMobile = (change.mqAlias == 'xs') || (change.mqAlias == 'sm');

      this.isMobile = isMobile;
      this._mode = (isMobile) ? 'over' : 'push';
      this._closeOnClickOutside = (isMobile) ? true : false;
      this._showBackdrop = (isMobile) ? true : false;
      this._opened = !isMobile;
      this.sidebarClosed = false;
    });

    this._routerEventsSubscription = this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd && this.isMobile) {
        this.sidenav.close();
      }
    });

    // Add slideDown animation to dropdown
    $('.dropdown').on('show.bs.dropdown', function (e) {
      $(this).find('.dropdown-menu').first().stop(true, true).slideDown(500);
    });

    // Add slideUp animation to dropdown
    $('.dropdown').on('hide.bs.dropdown', function (e) {
      $(this).find('.dropdown-menu').first().stop(true, true).slideUp(500);
    });

    //Add class on focus of search box in header
    $('.search-form input').focus(function () {
      $(this).parent().addClass('search-active');
    }).blur(function () {
      $(this).parent().removeClass('search-active');
    });
  }


  ngOnDestroy() {
    this._router.unsubscribe();
    this._mediaSubscription.unsubscribe();
  }

  isFullscreen: boolean = false;

  toggleFullscreen() {
    if (screenfull.enabled) {
      screenfull.toggle();
      this.isFullscreen = !this.isFullscreen;
    }
  }

  customizerFunction() {
    this.customizerIn = !this.customizerIn;
  }

  chatWindowFunction() {
    this.chatWindowOpen = !this.chatWindowOpen;
  }

  sidebarClosedFunction() {
    this.sidebarClosed = !this.sidebarClosed;
  }

  changeThemeColor(color) {
    this.themeSkinColor = color;
  }

  onActivate(e, scrollContainer) {
    scrollContainer.scrollTop = 0;
  }

  // Timer : Scheduled :
  private loadTimerScheduled() {
    if (this.isLoggedIn() == true) {
      this.timer = Observable.interval(120000).subscribe(() => {
        console.log('Main.component.ts : check facturations and show notification every 120 seconds');
        //TODO : check facturations and show notification
        this.showWarning("Cron_Check : " + new Date().toUTCString().substring(4, 25));
      })
    } else {
      this.timer.unsubscribe();
    }
  }

}





