import {BrowserModule} from '@angular/platform-browser';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {HttpModule, Http} from '@angular/http';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {NgModule, Injector} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {MaterialModule} from '@angular/material';
import {FlexLayoutModule} from '@angular/flex-layout';
import {NgbModalModule} from '@ng-bootstrap/ng-bootstrap';
import {AgmCoreModule} from '@agm/core';
import {TourNgBootstrapModule} from 'ngx-tour-ng-bootstrap';

import {PerfectScrollbarModule, PerfectScrollbarConfigInterface} from "ngx-perfect-scrollbar";
import {TranslateModule, TranslateLoader, TranslateStaticLoader} from 'ng2-translate/ng2-translate';
import {SidebarModule} from 'ng-sidebar';
import {Ng2BreadcrumbModule, BreadcrumbService} from 'ng2-breadcrumb/ng2-breadcrumb';
import 'hammerjs';

import {ChankyaAppComponent} from './app.component';
import {AppRoutes} from "./app-routing.module";
import {MainComponent}   from './main/main.component';
import {AuthComponent}   from './auth/auth.component';
import {HorizontalLayoutComponent} from './horizontal-layout/horizontal-layout.component';
import {MenuToggleModule} from './core/menu/menu-toggle.module';
import {MenuItems} from './core/menu/menu-items/menu-items';
import {PageTitleService} from './core/page-title/page-title.service';
import {AuthGuard} from "./auth/auth-guard.service";
import {AuthService} from "./auth/auth.service";
import {SettingsService} from "./core/settings/settings.service";
import {AuthHttpService} from "./auth/auth-http.service";
import {ToastrModule} from 'ngx-toastr';
import {ServiceLocator} from "./core/service-locator";
import {CompteService} from "./auth/compte/compte.service";
import { LoadingSpinnerModule } from './loading_spinner/loading-spinner';
import { FileUploader } from 'ng2-file-upload';

export function createTranslateLoader(http:Http) {
  return new TranslateStaticLoader(http, 'assets/i18n', '.json');
}

const perfectScrollbarConfig:PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserModule,
    BrowserAnimationsModule,
    LoadingSpinnerModule,
    ToastrModule.forRoot({
      timeOut: 5000,
      positionClass: 'toast-top-right',
      preventDuplicates: true,
    }),
    MaterialModule,
    SidebarModule.forRoot(),
    RouterModule.forRoot(AppRoutes),
    TourNgBootstrapModule.forRoot(),
    FlexLayoutModule,
    NgbModalModule.forRoot(),
    Ng2BreadcrumbModule.forRoot(),
    AgmCoreModule.forRoot({apiKey: 'AIzaSyBtdO5k6CRntAMJCF-H5uZjTCoSGX95cdk'}),
    PerfectScrollbarModule.forRoot(perfectScrollbarConfig),
    MenuToggleModule,
    HttpModule,
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: (createTranslateLoader),
      deps: [Http]
    }),
  ],
  declarations: [
    ChankyaAppComponent,
    MainComponent,
    AuthComponent,
    HorizontalLayoutComponent
  ],
  entryComponents: [],
  bootstrap: [ChankyaAppComponent],
  providers: [
    MenuItems,
    AuthGuard,
    AuthService,
    SettingsService,
    AuthHttpService,
    BreadcrumbService,
    PageTitleService,
    CompteService,
  ]
})
export class ChankyaAppModule {
  constructor(private injector:Injector) {
    ServiceLocator.injector = this.injector;
  }
}



