import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Observer } from 'rxjs/Observer';
import 'rxjs/add/operator/share';

/**
 * Injectable service
 * @export
 * @class LoadingSpinnerService
 */
@Injectable()
export class LoadingSpinnerService {
    /**
     * Spinner observer
     * @private
     * @type {Observer<any>}
     * @memberof LoadingSpinnerService
     */
    private spinnerObserver: Observer<any>;
    /**
     * Spinner observable
     * @type {Observable<any>}
     * @memberof LoadingSpinnerService
     */
    public spinnerObservable: Observable<any>;
    /**
     * Creates an instance of LoadingSpinnerService.
     * @memberof LoadingSpinnerService
     */
    constructor() {
        this.spinnerObservable = new Observable(observer => {
            this.spinnerObserver = observer;
        }
        ).share();
    }
    /**
     * To show spinner
     * @memberof LoadingSpinnerService
     */
    show() {                
        if (this.spinnerObserver) {
            this.spinnerObserver.next(true);
        }
    }
    /**
     * To hide spinner
     * @memberof LoadingSpinnerService
     */
    hide() {
        if (this.spinnerObserver) {
            this.spinnerObserver.next(false);
        }
    }
}



