import { Routes } from '@angular/router';

import { MainComponent } from './main/main.component';
import { AuthComponent } from './auth/auth.component';
import { HorizontalLayoutComponent } from './horizontal-layout/horizontal-layout.component';
import { AuthGuard } from "./auth/auth-guard.service";

export const AppRoutes: Routes = [{
  path: '',
  redirectTo: 'dashboard',
  pathMatch: 'full',
},
{
  path: '',
  component: MainComponent,
  children: [{
    path: 'dashboard',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './dashboard/dashboard.module#DashboardModule'
  },
  {
    path: 'admin/entreprise',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/entreprise/entreprise.module#EntrepriseModule'
  },
  {
    path: 'admin/ville',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/ville/ville.module#VilleModule'
  },
  {
    path: 'admin/tiers',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/tiers/tiers.module#TiersModule'
  },
  {
    path: 'admin/service',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/service/service.module#ServiceModule'
  },
  {
    path: 'admin/banque',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/banque/banque.module#BanqueModule'
  },
  {
    path: 'admin/materiel',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/materiel/materiel.module#MaterielModule'
  },
  {
    path: 'admin/utilisateur',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/utilisateur/utilisateur.module#UtilisateurModule'
  },
  {
    path: 'admin/profil',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/profil/profil.module#ProfilModule'
  },
  {
    path: 'admin/profilRole',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_ADMIN'],
    },
    loadChildren: './admin/profilRole/profilRole.module#ProfilRoleModule'
  },
  /* {
     path: 'admin/role',
     canActivate: [AuthGuard],
     canActivateChild: [AuthGuard],
     data: {
       authorities: ['ROLE_ADMIN'],
     },
     loadChildren: './admin/role/role.module#RoleModule'
   }, */
  {
    path: 'client',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_USER', 'ROLE_CLIENT'],
    },
    loadChildren: './client/client.module#ClientModule'
  },
  {
    path: 'charge',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    data: {
      authorities: ['ROLE_USER', 'ROLE_CHARGE'],
    },
    loadChildren: './charge/charge.module#ChargeModule'
  },
  {
    path: 'maintenance',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './maintenance/maintenance.module#MaintenanceModule'
  },
  {
    path: 'installation',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './installation/installation.module#InstallationModule'
  },
  {
    path: 'reparation',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './reparation/reparation.module#ReparationModule'
  },
  {
    path: 'chiffrage',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './chiffrage/chiffrage.module#ChiffrageModule'
  },
  {
    path: 'facturation',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './facturation/facturation.module#FacturationModule'
  },
  {
    path: 'reglement',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './reglement/reglement.module#ReglementModule'
  },
  {
    path: 'intervention',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './intervention/intervention.module#InterventionModule'
  },
  {
    path: 'charge_installation',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './charge_installation/charge_installation.module#ChargeInstallationModule'
  },

  {
    path: 'app-spinner',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './loading_spinner/loadingSpinner.module#LoadingSpinnerModule'
  },

  ],
},
{
  path: 'horizontal',
  component: HorizontalLayoutComponent,
  children: [{
    path: '',
    canActivate: [AuthGuard],
    canActivateChild: [AuthGuard],
    loadChildren: './dashboard/dashboard.module#DashboardModule'
  },
    // {
    //   path: 'dashboard',
    //   loadChildren: './dashboard/dashboard.module#DashboardModule'
    // }, {
    //   path: 'inbox',
    //   loadChildren: './inbox/inbox.module#InboxModule'
    // }, {
    //   path: 'calendar',
    //   loadChildren: './calendar/calendar.module#CalendarDemoModule'
    // }, {
    //   path: 'ui-elements',
    //   loadChildren: './ui-elements/ui-elements.module#UiElementsModule'
    // }, {
    //   path: 'components',
    //   loadChildren: './components/components.module#ComponentDemoModule'
    // }, {
    //   path: 'icons',
    //   loadChildren: './icons/icons.module#IconsModule'
    // }, {
    //   path: 'forms',
    //   loadChildren: './forms/forms.module#FormsDemoModule'
    // },
    // {
    //   path: 'editor',
    //   loadChildren: './editor/editor.module#EditorModule'
    // }, {
    //   path: 'chart',
    //   loadChildren: './chart/charts.module#ChartDemoModule'
    // }, {
    //   path: 'dragndrop',
    //   loadChildren: './drag-and-drop/dragndrop.module#DragDropDemoModule'
    // }, {
    //   path: 'resizable',
    //   loadChildren: './resizable/resizable.module#ResizableDemoModule'
    // }, {
    //   path: 'ngx-toaster',
    //   loadChildren: './ngx-toaster/toaster.module#ToasterDemoModule'
    // }, {
    //   path: 'animation',
    //   loadChildren: './animation/animation.module#AnimationDemoModule'
    // }, {
    //   path: 'maps',
    //   loadChildren: './maps/maps.module#MapsDemoModule'
    // }, {
    //   path: 'user-pages',
    //   loadChildren: './user-pages/users.module#UsersDemoModule'
    // }, {
    //   path: 'pages',
    //   loadChildren: './custom-pages/pages.module#PagesDemoModule'
    // }, {
    //   path: 'blog',
    //   loadChildren: './blog/blogs.module#BlogsDemoModule'
    // }, {
    //   path: 'gallery',
    //   loadChildren: './gallery/gallery.module#GalleryDemoModule'
    // }, {
    //   path: 'ecommerce',
    //   loadChildren: './ecommerce/ecommerce.module#EcommerceDemoModule'
    // }
  ],
},
{
  path: '',
  component: AuthComponent,
  children: [{
    path: 'session',
    loadChildren: './session/session.module#SessionDemoModule'
  }]
}];




