import {Injectable} from "@angular/core";
import {Response} from "@angular/http";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import {AuthHttpService} from "./auth-http.service";
import {JwtHelper} from "./JwtHelper";
import {Router} from "@angular/router";
import {SettingsService} from "../core/settings/settings.service";

@Injectable()
export class AuthService {
  redirectUrl: string;
  usernameForCompte: string;

  constructor(private settings: SettingsService,
              private authHttp: AuthHttpService,
              private router: Router) {
  }

  login(username: string, password: string): Observable<Response> {
    let body = JSON.stringify({
      username: username,
      password: password,
    });

    this.usernameForCompte = username;
    return this.authHttp.post(this.settings.loginUrl, body)
      .map(this.extractData)
      .catch(this.handleError);
  }

  extractData(res: Response) {
    let token = res.headers.get('Authorization');
    if (token) {
      localStorage.setItem('jwt', token);
    } else {
      localStorage.removeItem('jwt');
    }
    return true;
  }

  handleError(error: Response | any) {
    let errMsg: string;

    switch (error.status) {
      case 401:
        errMsg = "Utilisateur ou mot de passe incorrect !";
        break;
      case 0:
        errMsg = "Un problme a t gnr lors d'une connexion avec le serveur !";
        break;
      default:
        errMsg = error.message ? error.message : error.toString();
    }
    return Observable.throw(errMsg);
  }

  removeToken(): void {
    localStorage.removeItem('jwt');
  }

  logout(): void {
    this.removeToken();
    this.router.navigate(['/session/loginone']);
  }

  isLoggedIn(): boolean {
    let isLogged: boolean = false;
    let token: string = localStorage.getItem('jwt');

    if (token !== null) {
      isLogged = this.tokenNotExpired(token);
      if (isLogged === false) {
        this.removeToken();
      }
    }

    return isLogged;
  }

  tokenNotExpired(token: string): boolean {
    const jwtHelper = new JwtHelper();
    return !jwtHelper.isTokenExpired(token);
  }
}



