import { Injectable } from '@angular/core';
import { MenuService } from "../service/menu.service";
import { BaseComponent } from "../../component/base-component";
import { ToastrService } from 'ngx-toastr';
import { ServiceLocator } from '../../service-locator';

export interface ChildrenItems {
  state: string;
  name: string;
  type?: string;
  icon: string;
  authorities?: string[];
}

export interface Menu {
  state: string;
  name: string;
  type: string;
  icon: string;
  authorities?: string[];
  children?: ChildrenItems[];
}

let MENUITEMS = [
  /*
  {
    state: 'dashboard',
    name: 'DASHBOARD',
    type: 'link',
    icon: 'icon-home icons'
  },

    {
      state: 'admin',
      name: 'ADMIN',
      type: 'sub',
      authorities: ['ADMIN'],
      icon: 'icon-settings icons',
      children: [
        { state: 'banque', name: 'BANQUE', icon: 'icon icon-credit-card', authorities: ['ADMIN'], },
        { state: 'ville', name: 'VILLE', icon: 'icon icon-direction', authorities: ['ADMIN'], },
        { state: 'tiers', name: 'TIERS', icon: 'icon icon-bag', authorities: ['ADMIN'], },
        { state: 'service', name: 'SERVICE', icon: 'icon icon-tag', authorities: ['ADMIN'], },
        { state: 'materiel', name: 'MATERIEL', icon: 'icon icon-paper-clip', authorities: ['ADMIN'], },
        { state: 'utilisateur', name: 'UTILISATEUR', icon: 'icon icon-user', authorities: ['ADMIN'], },
        { state: 'profil', name: 'PROFIL', icon: 'icon icon-link', authorities: ['ADMIN'], },
      ]
    },
  
    {
      state: 'client',
      name: 'CLIENT',
      type: 'link',
      icon: 'icon-people icons',
    },
    {
      state: 'installation',
      name: 'NOUVELLE INSTALLATION',
      type: 'link',
      icon: 'icon-crop icons',
    },
    {
      state: 'maintenance',
      name: 'MAINTENANCE',
      type: 'link',
      icon: 'icon-layers icons',
    },
    {
      state: 'reparation',
      name: 'REPARATION',
      type: 'link',
      icon: 'icon-wrench icons',
    },
    {
      state: 'facturation',
      name: 'FACTURATION',
      type: 'link',
      icon: 'icon-drawer icons',
    },
    {
      state: 'charge',
      name: 'CHARGE',
      type: 'link',
      icon: 'icon-share-alt icons',
    },
    {
      state: 'intervention',
      name: 'INTERVENTION',
      type: 'link',
      icon: 'icon-support icons',
    },
  
    {
      state: 'report',
      name: 'RAPPORT',
      type: 'sub',
      icon: 'icon-book-open icons',
      children: []
    },
  */

];


@Injectable()
export class MenuItems {

  getAll(): Menu[] {
    return MENUITEMS;
  }

  add(menu: Menu) {
    MENUITEMS.push(menu);
  }

  private toastrService: ToastrService;

  constructor(private menuService: MenuService) {
    this.toastrService = ServiceLocator.injector.get(ToastrService);
  }

  loadMenu() {
    this.menuService.getMenuBean()
      .subscribe(menuList => {
        MENUITEMS = [];
        for (let menu of menuList) {
          this.add(menu);
        }
      }, error => this.toastrService.error(error.json().message)
      )
      ;
  }

}



