import { Injectable } from "@angular/core";
import { Response } from "@angular/http";
import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/catch";
import "rxjs/add/operator/map";
import { SettingsService } from "../../core/settings/settings.service";
import { AuthHttpService } from "../../auth/auth-http.service";
import { Compte } from "../metier/compte.model";
import { Router } from "@angular/router";
import { MenuItems } from "../../core/menu/menu-items/menu-items";

@Injectable()
export class CompteService {

  compte: Compte;
  autorisations: string[];

  constructor(private settings: SettingsService,
    private authHttp: AuthHttpService,
    private router: Router,
    public menuItems: MenuItems) {
  }

  hasAuthority(value: string | string[]): boolean {
    this.autorisations = typeof value === 'string' ? [<string>value] : <string[]>value;
    return this.hasAnyAuthority(this.autorisations)
  }

  private hasAnyAuthority(autorisations: string[]): boolean {

    if (autorisations === undefined) {
      return true;
    }

    if (this.isLoggedIn() === true) {
      if (this.compte === undefined) {
        console.log("Compte is  undefined");
        this.logout();
        return false;
      }

      if (this.compte.superAdmin == true) {
        return true;
      }

      for (let i = 0; i < autorisations.length; i++) {
        if (this.compte.roles.indexOf(autorisations[i]) !== -1) {
          return true;
        }
      }
      return false;
    } else {
      return true;
    }
  }

  private logout(): void {
    localStorage.removeItem('jwt');
    this.router.navigate(['/session/loginone']);
  }

  private getCompte(username: string): Observable<Compte> {
    return this.authHttp.post(this.settings.baseUrl + `/compte/getCompte`, JSON.stringify(username)).map((res: Response) => res.json());
  }

  addCompteAfterLogin(username: string) {
    this.getCompte(username).subscribe(compte => {
      this.compte = compte;
      this.autorisations = [''];
      this.menuItems.loadMenu();
    })
  }

  private isLoggedIn(): boolean {
    let isLogged: boolean = false;
    let token: string = localStorage.getItem('jwt');

    if (token !== null) {
      isLogged = true;
    }
    return isLogged;
  }

  getUserPhoto() {
    let photo = "assets/img/user/user.png";
    if (this.compte != null) {
      if (this.compte.photo != null) {
        photo = this.compte.photo;
      }
    }
    return photo;
  }

}



